@import '../dev/variables';
@import '../dev/mixins';
section {
  &#video {
    display: table;
    width: 100%;
    height: 100%;
    background-color: #eee;
    position: relative;
    z-index: 5;
    overflow: hidden;
    > .video-container {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      &:after {
        content: "";
        position: absolute;
        background: url(../images/video_filter.png) repeat;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 9999; }
      > .video {
        > img, video, canvas {
          width: 100% !important;
          height: 100% !important; } } }
    > .video-content {
      display: table-cell;
      vertical-align: middle;
      text-align: center;
      .title {
        color: #ffffff; }
      .title-sub {
        color: #ffffff; }
      .buttons-bar {
        margin: 40px auto;
        .btn {
          + .btn {
            margin-left: 15px; } } }
      .links-bar {
        margin: 40px auto; } } } }

