@import '../dev/variables';
@import '../dev/mixins';
section {
  &#tab {
    padding: 85px 0 59px;
    position: relative;
    z-index: 1;
    background-color: #fff;
    &:before, &:after {
      content: " ";
      position: absolute;
      top: 0;
      bottom: 0;
      width: 50%;
      z-index: -1;
      max-width: 960px; }
    &:before {
      background: url(../images/strategy_left.jpg)  110% bottom no-repeat;
      background-size: cover;
      left: 0; }
    &:after {
      background: url(../images/strategy_right.jpg) -10% bottom no-repeat;
      background-size: cover;
      right: 0; } } }

