@import '../dev/mixins';
@import '../dev/variables';

header#header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $top-nav-height;
    background-color: #000;
    padding: 13px 60px 10px;
    display: table;
    width: 100%;
    z-index: 999999;
    #nav-brand {
        display: table-cell;
        vertical-align: middle;
        width: 244px;
        > img {
            width: 244px; } }
    nav#nav {
        display: table-cell;
        vertical-align: middle;
        text-align: right;
        > ul {
            list-style: none;
            > li {
                display: inline-block;
                vertical-align: middle;
                > a {
                    color: #fff;
                    font-size: 14px;
                    font-weight: 600;
                    display: inline-block;
                    vertical-align: middle;
                    padding: 0 5px; } } } } }
