@import '../dev/variables';
@import '../dev/mixins';

.btn {
    @include transition(all 0.3s ease-in-out);
    font-size: 14px;
    display: inline-block;
    vertical-align: middle;
    padding: 12px 11px 10px;
    border: none;
    border-radius: 2px;
    text-decoration: none;
    font-weight: $bold;
    box-sizing: border-box;
    text-decoration: none;
    &.btn-blue {
        background-color: $blue;
        color: #fff;
        border: 1px solid $blue; }
    &.btn-lightblue {
        background-color: $lightblue;
        color: #fff;
        border: 1px solid $lightblue; }
    &.btn-red {
        background-color: $red;
        color: #fff;
        border: 1px solid $red; }
    &.btn-white {
        background-color: $red;
        color: #000;
        border: 1px solid #fff;
        &.btn-outside {
            color: #fff; } }
    &.btn-outside {
        background-color: transparent; }
    &.btn-shadow {
        @include box-shadow(0 15px 34px rgba(27,118, 190, 0.34)); }
    &:focus {
        outline: none; } }

.link {
    text-decoration: underline;
    color: #fff;
    font-weight: $bold;
    font-size: 12px; }

.links-bar {
    ul {
        li {
            display: inline-block;
            vertical-align: middle;
            margin: 0 15px;
            font-size: 12px;
            font-weight: 600;
            text-transform: uppercase; } } }


.social-bar {
    ul {
        &:after {
            content: "";
            @include clearfix(); }
        li {
            display: inline-block;
            vertical-align: middle;
            .icon {
                width: 28px;
                height: 28px;
                line-height: 28px;
                border-radius: 28px;
                background-color: $blue;
                color: #fff;
                display: block; } } } }
