@import '../dev/variables';
@import '../dev/mixins';

.image-box {
    margin-bottom: 60px;
    img {
        display: block;
        margin: 0 auto;
        max-width: 100%;
        height: 56px; }
    > .title {
        color: $text-color;
        padding: 27px 0 20px;
        position: relative;
        text-transform: none;
        font-size: 20px;
        &:first-letter {
            color: $blue;
            font-size: 20px; } }
    > p {
        color: $text-color;
        letter-spacing: 0.025rem;
        line-height: 1.6;
        font-size: 14px; } }
