@import "dev/variables.sass";
@import "dev/mixins.sass";

@import "font.sass";
@import "../icon-fonts/style.css";

* {
  margin: 0;
  padding: 0; }

html, body, .main {
  height: 100%; }

body {
  background-color: $bg-color;
  font-family: $font-family; }

.color-red {
  color: $red !important; }

.color-white {
  color: #fff !important; }

.color-black {
  color: #163e51 !important; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

a {
  text-decoration: none;
  &:hover {
    text-decoration: none; }
  &:focus {
    outline: none; } }

input, textarea {
  &:focus {
    outline: none; } }

ul {
  list-style: none; }

.icon {
  display: inline-block;
  vertical-align: middle; }

.text-center {
  text-align: center; }

.vcenter {
   display: inline-block;
   vertical-align: middle;
   float: none; }

.logo {
  display: inline-block;
  vertical-align: middle;
  > img {
    max-width: 100%;
    display: block; } }

@import "partial/header.sass";

#banner {
  position: relative;
  z-index: 1; }

.table-layout {
  display: table;
  width: 100%;
  text-align: center;
  .col {
    display: table-cell;
    vertical-align: middle;
    img {
      max-width: 100%;
      width: auto; } } }

@import "components/components";
@import "partial/footer.sass";
