@import '../dev/variables';
@import '../dev/mixins';

.tab {
    nav.tab-menu {
        .btn {
            @include transition(all .5s);
            background: transparent;
            border: 1px solid #3389b3;
            color: #3389b3;
            &.active {
                background-color: #3389b3;
                color: #fff; } } }
    .tab-content {
        display: none;
        &.active {
            display: block; } } }
