@import '../dev/variables';
@import '../dev/mixins';

a.popupvideo {
    figure {
        width: 100%;
        position: relative;
        margin-bottom: 20px;
        > img {
            width: 100%;
            display: block; }
        &:after {
            @include scale(1);
            @include transition(all .2s ease-in-out);
            content: "\f144";
            font-family: 'icon' !important;
            speak: none;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            line-height: 1;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            width: 102.5px;
            height: 102.5px;
            color: rgb(217, 94, 107);
            font-size: 102.5px;
            position: absolute;
            top: 50%;
            margin-top: -21.25px;
            left: 50%;
            margin-left: -51.25px;
            border-radius: 50%; }
        &:hover {
            &:after {
                @include box-shadow(0 12px 57px rgba(217, 94, 107, 0.71));
                @include scale(1.5); } }
        figcaption {
            color: #fff;
            font-size: 12px;
            font-weight: 400;
            position: absolute;
            width: 100%;
            text-align: center;
            left: 0;
            bottom: 80px; } } }


